.navbar {
  background-image: -webkit-radial-gradient(circle, #444, #000 60%);
  background-image: radial-gradient(circle, #444, #000 60%);
  position: fixed;top:0px;left:0px;
  width: 100%;
  z-index: 1;
}

.logo {
  padding: 0px;
  width: 25%;
}

.logo img {
  height: 100px;
}

.instructions {
  font-size: 16px;
  width: 50%;
  text-align: center;
}

.scoreboard {
  color: #ddd;
  width: 25%;
  font-size: 16px;
  text-align: right;
}

@media screen and (min-width: 576px) {
  .instructions {
    font-size: 18px;
  }
  .scoreboard {
    font-size: 18px;
  }
}

@media screen and (min-width: 768px) {
  .instructions {
    font-size: 20px;
  }
  .scoreboard {
    font-size: 20px;
  }
}

@media screen and (min-width: 992px) {
  .instructions {
    font-size: 22px;
  }
  .scoreboard {
    font-size: 22px;
  }
}

@media screen and (min-width: 1200px) {
  .instructions {
    font-size: 24px;
  }
  .scoreboard {
    font-size: 24px;
  }
}
body {
  background-image: url(/clicky-game/static/media/background.1f20f50e.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.container-fluid {
  padding: 25px 0px;
  margin-top: 100px;
  height: 100vh;
}

@media screen and (min-width: 768px) {
  .container-fluid {
    width: 80%;
  }
}

@media screen and (min-width: 992px) {
  .container-fluid {
    width: 70%;
  }
}

@media screen and (min-width: 1200px) {
  .container-fluid {
    width: 100%;
  }
}
.imgContainer {
  width: 33%;
  float: left;
  padding: 15px;
}

.token {
  width: 100%;
}

.hovereffect {
  overflow:hidden;
  position:relative;
  cursor:default;
}

.hovereffect img {
  display:block;
  position:relative;
  -webkit-transition:all .2s linear;
  transition:all .2s linear;
}

.hovereffect:hover img {
  -webkit-transform:scale(1.2);
  transform:scale(1.2);
}

@media screen and (min-width: 576px) {
  .imgContainer {
    width: 25%;
  }
}

@media screen and (min-width: 1200px) {
  .imgContainer {
    width: 16.5%;
  }
}
.footer {
  background-image: -webkit-radial-gradient(circle, #444, #000 40%);
  background-image: radial-gradient(circle, #444, #000 40%);
  clear: both;
}

.lead {
  font-size: 14px;
  text-align: center;
  color: #fff;
  -webkit-text-stroke-width: .5px;
  -webkit-text-stroke-color: #f00;
}
